@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

//Reset
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  //This defines 1rem (10px) 62,5% of initial 16px
  font-size: 62.5%;
  font-family: 'Nunito', sans-serif;
}

body {
  position: relative;
  min-height: 100vh;
  background-image: url('./../images/wall.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.overlay {
  min-height: 100vh;
  background-color: rgba(17, 17, 17, 0.73);
}

.glow-sign {
  position: absolute;
  top: 10%;
  left: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__span {
    font-size: 6rem;
    color: #b2b2b0;
    animation: glow 0.08s ease-in-out infinite alternate;
  }
}

.container-cta {
  position: absolute;
  top: 30%;
  right: 50%;
  transform: translate(50%, -30%);
}

.form {
  display: flex;
  flex-direction: column;

  &__label {
    margin: 2rem 0 1rem 0;
    font-size: 2rem;
    font-weight: bolder;
    color: white;
    text-align: start;
  }

  &__select {
    font-size: 1.6rem;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: 1rem 2.4rem 1rem 1rem;
    width: 100%;
    border: 1px solid #aaa;
    border-radius: 1rem;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    background-position: right 0.7em top 50%, 0 0;
    background-size: 0.65rem auto, 100%;

    &:focus {
      box-shadow: 0 0 0 0.1rem;
      color: rgb(0, 0, 0);
      outline: none;
    }
  }
}

.btn {
  font-size: 2.5rem;
  padding: 1.5rem 7rem;
  margin-top: 5rem;
  border-radius: 1rem;
  text-transform: uppercase;
  transition: all 0.2s ease;
  cursor: pointer;

  &:hover {
    transform: translateY(0.2rem);
    box-shadow: -1px 3px 15px -2px rgba(238, 123, 15, 0.96);
  }

  &:focus {
    outline: none;
  }
}

.container-quiz {
  display: none;
  width: 60%;
  justify-content: center;
  margin: 0 auto;
  margin-top: 10rem;
  border-radius: 2rem;
  background-color: rgba(255, 255, 255, 0.8);
}

.quiz {
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
  &__category {
    font-size: 2rem;
    color: rgb(95, 95, 95);
    padding: 2rem 0;
  }

  &__question {
    font-size: 2rem;
    padding: 1rem 0 3rem 0;
  }

  &__list {
    padding-bottom: 8rem;
  }

  &__list-item {
    font-size: 2rem;
    list-style: none;
    background-color: #fff;
    margin-top: 1rem;
    padding: 2rem 1rem;
    text-align: center;
    border-radius: 2rem;
  }

  &__nextBtn {
    padding: 2rem 3rem;
  }
}

.progress-bar {
  display: none;
  align-items: center;
  width: 50%;
  background-color: rgb(128, 128, 128);
  margin: 0 auto;
  margin-top: 5rem;
  height: 2rem;
  border-radius: 2rem;

  &__bar {
    width: 15%;
    height: 1rem;
    background-color: #f3ac08;
    border-radius: 2rem 1rem 1rem 2rem;
  }
}

.correct-answer {
  background-color: rgb(8, 255, 8);
  border: 5px solid black;
}

.incorrect-answer {
  background-color: red;
  border: 5px solid black;
}

.disabled {
  pointer-events: none;
  opacity: 0.6;
}

@keyframes glow {
  from {
    text-shadow: 0 0 6px rgba(202, 228, 225, 0.92),
      0 0 30px rgba(202, 228, 225, 0.34), 0 0 12px rgba(248, 146, 13, 0.52),
      0 0 21px rgba(242, 168, 30, 0.92), 0 0 34px rgba(238, 167, 13, 0.78),
      0 0 54px rgba(242, 168, 30, 0.92);
  }
  to {
    text-shadow: 0 0 6px rgba(202, 228, 225, 0.98),
      0 0 30px rgba(202, 228, 225, 0.42), 0 0 12px rgba(248, 146, 13, 0.58),
      0 0 22px rgba(242, 168, 30, 0.84), 0 0 38px rgba(238, 167, 13, 0.88),
      0 0 60px rgba(242, 168, 30, 1);
  }
}
